#works{
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
    margin: 0 auto;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
}
.my{
    font-weight: 700;
    font-size: 50px;
    line-height: 1.5;
    color: white;
    text-align: center;
    margin-bottom: 30px;
}
.worktitle{
    color: yellow;
}
.workdes{
    font-size: 1rem;
    font-weight: 300;
    margin-bottom: 50px;
    max-width: 45rem;
}
.worksImgs{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    flex-wrap: wrap;
    max-width: 65rem;
}
.worksImg{
    object-fit: cover;
    height: 20rem;
    margin: 0.5rem;
}
.worksBtn{
    padding: 0.5rem 2.5rem;
    border: none;
    border-radius: 2rem;
    background: white;
    font-size: 1rem;
    margin-top: 30px;
}
@media screen and (max-width: 585px){
    .worksImg{
        height: 48vw;
    }
}
.worksImgs button {
    border: none; /* Remove the default button border */
    padding: 0; /* Remove padding to make the button fit the image */
    background: none; /* Remove background color */
    cursor: pointer; /* Change cursor to pointer on hover */
    position: relative;
}
  
  .worksImgs button:focus {
    outline: none; /* Remove focus outline */
  }
  .worksImgContainer button:hover::after {
    content: 'Project Preview';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: yellow; /* Yellow overlay with opacity */
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px; /* Adjust font size */
    font-weight: bold; /* Adjust font weight */
    color: black; 
}

  
  
  .worksImgContainer {
    position: relative;
    cursor: pointer;
}
  .line {
    width: 30%; 
    height: 0.5px; 
    background-color: rgb(21, 53, 65); 
    margin: 0 auto;
    margin-top: 100px;  
}