.timeline {
    font-weight: 700;
    font-size: 28px;
    line-height: 1.5;
    color: white;
    text-align: center;
  }
  
  .icon-background {
    background-color: yellow; /* Adjust background color as needed */
    width: 50px; /* Adjust width and height as needed for the circle size */
    height: 50px;
    border-radius: 50%; /* Makes the shape round */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .icon {
    color: black; /* Adjust icon color as needed */
    font-size: 24px; /* Adjust icon size as needed */
  }
  
  .date {
    display: inline-block;
    padding: 6px 12px;
    background-color: #6a6767;
    color: white;
    border-radius: 16px;
    font-size: 14px;
    font-weight: 500;
    margin-right: 8px;
    margin-top: 8px;
  }
  
  .intern {
    font-size: 20px;
    font-weight: 700;
  }
  
  .experience {
    margin-top: 30px;
    margin-left: 280px;
    margin-right: 600px;
    padding: auto;
  }
  
  .company {
    font-size: 20px;
    font-weight: 500;
    color: #827d7d;
    margin-left: 5px; 
  }
  
  .icon-column {
    display: inline-block;
    width: 50px;
  }
  
  .date-column {
    display: inline-block;
    vertical-align: top;
    margin-left: 25px;
  }
.description{
    margin-left: 78px;
    color: #827d7d;
    font-size: 16px;
    letter-spacing: 1px;
    font-weight: 500;
}
.role{
    display: flex;
    margin-left: 78px;
    padding-top: 10px; 
    padding-bottom: 10px;
}
