.contact-card {
    width: 400px;
    height: 350px;
    padding: 20px;
    border-radius: 10px;
    background-color: rgb(74, 73, 73);
    margin-left: 350px; /* Adjusted margin-left */
    margin-top: 50px;
    justify-content: space-between;
  }
  
  .QA {
    margin-top: 15px;
    font-size: 18px;
    letter-spacing: 1px;
    font-weight: 500; 
    color: #9f9393; 
    margin-left: 15px;
    margin-right: 15px;  
  }
  
  .icons {
    color: yellow;
    margin-left: 15px;
    margin-right: 15px;
  }
  
  .contact-info {
    margin-top: 20px;
  }
  
  .phone, .email {
    font-weight: 500;
  }
  
  .contact {
    font-weight: 700;
    font-size: 50px;
    line-height: 1.5;
    color: white;
    text-align: center;
    margin-top: 80px;
  }
  
  .me {
    color: yellow;
  }
  
  form {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
  }
  
  .personal-details, .personal-detail {
    margin-bottom: 20px;
  }
  
  input[type="text"],
  input[type="email"],
  textarea {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    background-color: rgb(74, 73, 73);
    color: white;
    box-sizing: border-box;
  }
  
  textarea {
    height: 150px;
  }
  
  input[type="submit"] {
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
  }
  
  input[type="submit"]:hover {
    background-color: #0056b3;
  }
  
  .personal-details {
    display: flex;
  }
  
  .personal-details input[type="text"],
  .personal-details input[type="email"] {
    flex: 1;
    margin-right: 10px;
  }
  
  .contact-container {
    display: flex;
  }
  
  .contact-form {
    flex: 1; 
    margin-top: 50px;
    margin-left: 50px; /* Adjusted margin-left */
  }
  button[type="submit"] {
    background-color: white; /* Blue color */
    color: black;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
  }
  
  button[type="submit"]:hover {
    background-color: #0056b3; /* Darker blue color on hover */
  }