#intro{
    height: calc(100vh-4rem);
    width: 100vw;
    max-width: 75rem;
    overflow: hidden;
    margin: 0 auto;
    position: relative;

}
.bg{
    position: absolute;
    top: -4rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    background: transparent;
    width: 40%;
    height: auto;
    margin-top: 100px;
}
.introContent{
    height: 100vh;
    width: 100vw;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    margin-top: 200px;
     
}
.hello{
    font-size: 1.75rem;
    font-weight: 500;
}
.introName{
    color: yellow;
}
.introPara{
    font-size: medium;
    font-weight: 200;
    letter-spacing: 1px;
}
.btn{
    background-color: #0e0e1a;
    margin: 1rem 0;
    padding: 1rem 2.5rem;
    border: none;
    outline: none;
    border-radius: 0.4rem;
    cursor: pointer;
    width: 220px;
    font-size: 1.2rem;
    color: #eaeaea;
    text-transform: uppercase;
    font-weight: 700;
    transition: .6s;
    box-shadow: 0 0 60px #1f4c65;
    -webkit-box-reflect: below 10px linear-gradient(to bottom,rgba(0,0,0,0),rgba(0,0,0,.4));
}
.img{
    color: #eaeaea;
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}
@media screen and (max-width:840) {
    .bg{
        right: -10;
    }
    .introContent{
        font-size: 10vw;
    }
    .hello{
        font-size: 4.5vw;
    }
}
@media screen and (ma) {
    .bg{
        right: -20px;
    }
}
