*{
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
body{
    font-family: "Poppins", sans-serif;
}
.container{
    width: 100%;
    padding-right: 20px;
    padding-left: 20px;
    margin-left: auto;
    margin-right: auto;
}
@media (min-width: 768px){
    .container{
        margin: 0 auto;
    }
}
@media (min-width: 992px){
    .container{
        max-width: 960px;
    }
}
@media (min-width: 1200px){
    .container{
        max-width: 1140px;
    }
}
@media (min-width: 1140px){
    .container{
        max-width: 1350px;
    }
}
.section_title p{
    font-weight: 700;
    font-size: 50px;
    line-height: 1.5;
    text-align: center;
}
.section_title span{
    color: yellow;
}
.section_title.center{
    text-align: center;

}
.experience-section{
    padding: 65px 0 100px;
    position: relative;
}
.experience-section .experience-items{
    margin-top: 30px;
    display: -webkit-box;
    display: -ms-box;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: center;
    gap: 20px;
}
.experience-item{
    -webkit-box-flex: 0;
    -ms-flex: 0 0 38%;
    flex: 0 0 38%;
    max-width: 38%;
}
.experience-info{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}
.experience-info p{
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: white;
    opacity: 1;
    -webkit-animation: showText 0.5s 1s linear forwards;
    animation: showText 0.5s 1s linear forwards;
}
.progress-line{
    margin-top: 7px;
    height: 10px;
    width: 100%;
    background-color: #cdcdcd;
    position: relative;
}
.progress-line span{
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: left;
    transform-origin: left;
    -webkit-animation: animate 1s cubic-bezier(1,0,0.5,1) forwards;
    animation: animate 1s cubic-bezier(1,0,0.5,1) forwards;
    height: 100%;
    border-radius: 50px;
    position: absolute;
    background-color: yellow;
    transition: 1s all;
}
.progress-line span.animate{
    -webkit-animation: animate 1.5s 0.5s cubic-bezier(1,0,0.5,1) forwards;
    animation: animate 1.5s 0.5s cubic-bezier(1,0,0.5,1) forwards;
}
@-webkit-keyframes showText{
    100%{
        opacity: 1;
    }
}
@keyframes showText{
    100%{
        opacity: 1;
    }
}
@-webkit-keyframes animate{
    100%{
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
    }
}
@keyframes animate{
    100%{
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
    }
}
.btn{
    background-color: #0e0e1a;
    padding: 1rem 2.5rem;
    border: none;
    outline: none;
    border-radius: 0.4rem;
    cursor: pointer;
    width: 200px;
    font-size: 1.2rem;
    color: #eaeaea;
    text-transform: uppercase;
    font-weight: 700;
    transition: .6s;
    box-shadow: 0 0 60px #1f4c65;
    -webkit-box-reflect: below 10px linear-gradient(to bottom,rgba(0,0,0,0),rgba(0,0,0,.4));
}
.btn:hover{
background-color: #1f4c65;
}

.img{
    color: #eaeaea;
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}
